.MyBtn {
    padding: 5px 15px;
    background-color: white;
    color: black;
    border: 2px solid #555555;
    cursor: pointer;
}

.MyBtn:hover {
    background-color: #555555;
    color: white;
}

.MyBtn {
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
}